import { AiOutlineHeart } from "react-icons/ai"; 
import React,{useEffect,useState} from 'react';
import { useParams,  useNavigate } from 'react-router-dom';
import axios from 'axios';
import { Button, message } from 'antd';
import "./ProductPage.scss";
import { API_URL} from "../config/constants";
import dayjs from "dayjs";

const ProductPage = () => {
    const {id}=useParams();
    const navigate=useNavigate();
    const [product,setProduct]=useState(null);
    const getProduct=()=>{
        axios.get(`${API_URL}/products/${id}`)
        .then((result)=>{
            setProduct(result.data.product)
           /*  console.log(result) */
        })
        .catch((error)=>{
            console.log(error)
        })
    }

    useEffect(()=>{
        getProduct();
    },[])


    const onClickPurchase=()=>{
        axios.post(`${API_URL}/purchase/${id}`)
        .then((result)=>{
            message.info(` `)
            getProduct();
        })
        .catch((error)=>{
            message.error(`  ${error.message}`)
        })
    }

    console.log(product)
    if(product === null){
        return <h2>   ...</h2>
    }

    return (
        <div className="product-container">
            <button onClick={() => navigate(-1)} id='back-btn'></button>
            <div id="image-box">
                <img src={`${API_URL}/${product.imageUrl}`} alt={product.name} />
            </div>
            <div id="contents-box">
                <AiOutlineHeart  className="product-icon"/>
                <span className='product-seller'>{product.seller}</span>
                <div className="product-name">{product.name}</div>
                <div className="product-price">{product.price}</div>
                <div className="product-createAt">{dayjs(product.createdAt).format('YYYY MM DD')}</div>
                <div className="product-description">{product.description}</div>
                <Button className="payment" onClick={onClickPurchase} disabled={product.soldout === 1 ? true:false}></Button>
            </div>
        </div>
    );
};

export default ProductPage;