import { AiOutlineCamera } from "react-icons/ai"; 
import React, { useState } from 'react';
import { Button, InputNumber, Divider, Form, Input, Upload, message} from 'antd';
import "./UploadPage.scss"
import { API_URL } from "../config/constants";
import {useNavigate} from 'react-router-dom'
import axios from "axios";


const {TextArea}=Input;

const UploadPage = () => {
    const [imageUrl,setImageUrl]=useState(null)
    const history=useNavigate();
    const onSubmit  = (values) => {
        // console.log('Success:', values);
        axios.post(`${API_URL}/products`,{
            name:values.name,
            description: values.description,
            seller: values.seller,
            price:parseInt(values.price),
            imageUrl:imageUrl
        })
        .then((result)=>{
            console.log(result);
            history('/',{replace:true})
        })
        .catch(error=>{
            console.error(error);
            message.error(` . ${error.message}`)
        })
    };
    //  
    const onChangeImage=(info)=>{
        if(info.file.status === 'uploading'){
            return;
        }
        if(info.file.status === 'done'){
            const response=info.file.response;
            const imageUrl=response.imageUrl;
            setImageUrl(imageUrl)
        }
    }
    return (
        <div id='upload-container'>
            <Form name="uploadForm" onFinish={onSubmit}  initialValues={{price:0}}>
                <div className="upload-imgBox">
                    <Form.Item name="upload">
                        <Upload name="image" action={`${API_URL}/image`} listType="picture" showUploadList={false} onChange={onChangeImage}>  {/*  upload img */}
                            {
                                imageUrl ? (<img id='upload-img' src={`${API_URL}/${imageUrl}`} alt="img"/>):(
                                    <div id="upload-placeholder">
                                        <AiOutlineCamera className="icon-cam"/>
                                        <span>  </span>
                                    </div>
                                    )
                            }
                        </Upload>
                    </Form.Item>
                </div>
                <div className="upload-inputBox">
                    <Form.Item label={<span className="upload-label"></span>} name="name" rules={[{ required: true, message: "   ." }]}>
                        <Input className="upload-name" placeholder=" " size="large" />
                    </Form.Item>
                    <Divider />
                    <Form.Item label={<span className="upload-label"></span>} name="seller" rules={[{ required: true, message: "   ." }]}>
                        <Input className="upload-seller" placeholder=" " size="large" />
                    </Form.Item>
                    <Divider />
                    <Form.Item label={<span className="upload-price"></span>} name="price" rules={[{ required: true, message: "   ." }]} initialValue={0}>
                        <InputNumber className="upload-price" size="large" min={0} defaultValue={0} />
                    </Form.Item>
                    <Divider />
    
                    <Form.Item label={<span className="upload-label"></span>} name="description" rules={[{ required: true, message: "   ." }]}>
                        <TextArea size="large" id="product-description" showCount maxLength={300} placeholder=" "></TextArea>
                    </Form.Item>
                </div>
                <div className="upload-btn">
                    <Form.Item>
                        <Button id="submit-button" htmlType="submit" /* onClick={info} */>
                        
                        </Button>
                    </Form.Item>
                </div>
            </Form>
        </div>
    );
};

export default UploadPage;